
import { Text, Image, StyleSheet, View, Button, TouchableOpacity, ImagePropTypes, Touchable, ImageBackground, ScaledSize } from "react-native";
import { Dimensions } from 'react-native';
import AppLoading from 'expo-app-loading';
import {useFonts, PressStart2P_400Regular} from '@expo-google-fonts/press-start-2p'
import React, {useState} from 'react';


const HomeScreen = (props:React.Component) => {

    const [windowDim, setWindow] = useState(Dimensions.get('window'));
    window.addEventListener('resize', () => setWindow(Dimensions.get('window')))
    const BACKGROUND_HEIGHT = 867;
    const BACKGROUND_WIDTH = 1542;

    var RATIO = 0;
    var FONT_SIZE = 54*RATIO;
    var LINE_SPACING = 1.15;
    var LINE_HEIGHT = 0;
    var backgroundHeight = 0;
    var backgroundWidth =  0;
    var buttonBorderWidth = 0;

    var background_aspectRatio = BACKGROUND_WIDTH/BACKGROUND_HEIGHT;
    var window_aspectRatio = windowDim.width/windowDim.height;

    if (background_aspectRatio < window_aspectRatio) {
        RATIO = windowDim.height/BACKGROUND_HEIGHT;
        FONT_SIZE = 54*RATIO;
        LINE_HEIGHT = FONT_SIZE*LINE_SPACING;
        backgroundHeight = windowDim.height;
        backgroundWidth =  RATIO * BACKGROUND_WIDTH;
        buttonBorderWidth = 5*RATIO;
    }
    else {
        RATIO = windowDim.width/BACKGROUND_WIDTH;
        FONT_SIZE = 54*RATIO
        LINE_HEIGHT = FONT_SIZE*LINE_SPACING;
        backgroundHeight = RATIO * BACKGROUND_HEIGHT;
        backgroundWidth =  windowDim.width;
        buttonBorderWidth = 5*RATIO;
    }

    

    let [fontsLoaded] = useFonts({
        PressStart2P_400Regular,
      });
    
      if (!fontsLoaded) {
        return <AppLoading />;
      }

      const styles = stylefactory(backgroundHeight, backgroundWidth, buttonBorderWidth, FONT_SIZE, LINE_HEIGHT);

    return <View style={styles.viewStyle}>

        <ImageBackground style={styles.imageStyle} source={require('../../assets/backgroundBanner.gif')}>

            <TouchableOpacity style={styles.buttonStyle}>
                <Text style={styles.textStyle} >Choose Your Own Audioventure!</Text>                
            </TouchableOpacity>

        </ImageBackground>


    </View>
};


const stylefactory = (backgroundHeight:number, backgroundWidth:number, buttonBorderWidth:number, FONT_SIZE:number, LINE_HEIGHT:number) => StyleSheet.create({

    viewStyle: {
        // flexDirection: "column",
        justifyContent: 'center',
        backgroundColor: 'black',
        alignItems: 'center',
    },
    imageStyle: {
        justifyContent: 'center',
        alignItems: 'center',
        height: backgroundHeight,
        width: backgroundWidth,
        resizeMode: "center",
        backgroundColor: 'black',
        // ...StyleSheet.absoluteFillObject,
        overflow: 'hidden'
    },
    buttonStyle: {
        borderWidth: buttonBorderWidth,
        borderColor: '#b4a7d6',
        backgroundColor: '#8e7cc3c6',
        width: '50%',
        height: '25%',
        borderRadius: 3,
        justifyContent: 'center',
    },
    textStyle: {
        fontFamily: 'PressStart2P_400Regular',
        fontSize: FONT_SIZE,
        textAlign: 'center',
        color: 'white',
        opacity: 1,
        lineHeight: LINE_HEIGHT
    }
})


  


export default HomeScreen;

